import { Component, Input, OnInit } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { BarcodeFormat } from '@zxing/library';
import { BehaviorSubject } from 'rxjs';
import { AddProductComponent } from '../add-product/add-product.component';
import { ProductsService } from '../products.service';

@Component({
  selector: 'app-root',
  templateUrl: 'scanner.page.html',
  styleUrls: ['./scanner.page.scss']
})
export class ScannerPage{
  @Input() modalOpen = false;

  availableDevices: MediaDeviceInfo[];
  currentDevice: MediaDeviceInfo = null;

  formatsEnabled: BarcodeFormat[] = [
    BarcodeFormat.CODE_128,
    BarcodeFormat.DATA_MATRIX,
    BarcodeFormat.EAN_13,
    BarcodeFormat.QR_CODE,
  ];
  hasDevices: boolean;
  hasPermission: boolean;

  qrResultString: string;

  enabled = true;

  modal: HTMLIonModalElement = null;


  constructor(
    public modalController: ModalController,
    public productsService: ProductsService,
    public alertController: AlertController) {
  }
  ionViewWillEnter() {
    console.log('enter')
    this.enabled = true;
  }
  ionViewWillLeave() {
    console.log('leave')
    this.enabled = false;
  }
  async generateModal() {
    this.modal = await this.modalController.create({
      component: AddProductComponent,
      componentProps: {
        code: this.qrResultString
      }
    });
    await this.modal.present();
    let { data } = await this.modal.onWillDismiss();
    this.modalOpen = false;
  }

  clearResult(): void {
    this.qrResultString = null;
  }

  onCamerasFound(devices: MediaDeviceInfo[]): void {
    this.availableDevices = devices;
    this.hasDevices = Boolean(devices && devices.length);
  }

  onCodeResult(resultString: string) {
    this.qrResultString = resultString;
    console.log(resultString);
    var errorDialog = this.errorDialog.bind(this);
    var generateModal = this.generateModal.bind(this);
    if (this.modalOpen === false) {
      this.modalOpen = true;
      this.productsService.getProductDetails(resultString).then(function () {
        generateModal();
      }).catch(function (error) {
        errorDialog();
      })
    }
  }

  async errorDialog() {
    const alert = await this.alertController.create({
      header: 'Fehler',
      message: 'Das gescannte Produkt ist nicht in unserer Datenbank erfasst. MÃ¶chten Sie dies dem Entwickler melden?',
      buttons: [
        {
          text: 'Nein',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.modalOpen = false;
          }
        }, {
          text: 'Ja',
          handler: () => {
            window.location.href = "mailto:nutriinfo.app@gmail.com?subject=Fehlerbericht%20NutriInfo&body=Ein%20Fehler%20mit%20dem%20Barcode%20Scanner%20wurde%20erkannt.%20Der%20Barcode%20\"[]\"%20konnte%20nicht%20in%20unserer%20Datenbank%20gefunden%20werden.%0D%0ABitte%20geben%20Sie%20manuell%20den%20Namen%20des%20Produktes%20an:".replace("[]", this.qrResultString);
            this.modalOpen = false;
          }
        }
      ]
    });

    await alert.present().then(function () {
    }.bind(this))
  }

  onDeviceSelectChange(selected: string) {
    const device = this.availableDevices.find(x => x.deviceId === selected);
    this.currentDevice = device || null;
    console.log("new device", device)
  }

  onHasPermission(has: boolean) {
    this.hasPermission = has;
  }

  openInfoDialog() {
    const data = {
      hasDevices: this.hasDevices,
      hasPermission: this.hasPermission,
    };

    // this._dialog.open(AppInfoDialogComponent, { data });
  }
}
